.games {
  font-family: $font-title;
  margin-top: 2rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  transition: 2s;
  top: 50%;
  overflow: hidden;
  min-height: 45%;
  z-index: 0;

  @include phone {
    margin: 0;
    bottom: 0;
  }

  @include tablet {
    margin-top: 4rem;
  }

  @media(max-height: 600px) {
    margin-top: 4rem;
  }

  &__animation {
    top: 0;
    position: relative;

    @media (max-width: 500px) {
      width: 100%;
    }

    @media (min-width: 501px) and (max-width: 767px) {
      width: 70%;
    }
  }

  &__animation:nth-child(1) &__container:hover {
    background-color: $color-hover1;
  }

  &__animation:nth-child(2) &__container:hover {
    background-color: $color-hover2;
  }

  &__animation:nth-child(3) &__container:hover {
    background-color: $color-hover3;
  }

  &__animation:nth-child(4) &__container:hover {
    background-color: $color-hover4;
  }

  &__error {
    h2 {
      text-align: center;
      width: 100%;
    }
  }

  &__error, &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 1rem;
    transition: 1s;
    background-color: $color-text;
    color: $color-background;
    width: 35rem;
    position: absolute;
    z-index: 1;

    @include phone {
      width: 90%;
      margin: 0 auto;
    }

    h2 {
      font-size: 0.7rem;
      line-height: 1.5rem;
      width: 70%;
      text-shadow: 2px 2px 2px $color-background;

      @include phone {
        line-height: 1rem;
        font-size: 0.5rem;
      }
    }

    &__details {
      display: flex;
      justify-content: center;
      align-content: center;

      &__photos {
        width: 40%;

        img {
          border-radius: 50%;
          width: 3rem;
          height: 3rem;
          object-fit: cover;
        }

      }
    }
  }

  &__button {
    z-index: 20;

    .left, .right {
      position: absolute;
      top: 50%;
    }

    .left {
      left: 30%;

      @include phone {
        left: 2%;
      }

      @include tablet {
        left: 5%;
      }
    }

    .right {
      right: 30%;

      @include phone {
        right: 2%;
      }

      @include tablet {
        right: 5%;
      }

    }
  }
}
